.colors {
    position:fixed;
    right:0;
    bottom:-5.5em;
    text-align:right;
    transition-duration:.5s;
}
.colors.expand {
    /* display:none; */
    position:fixed;
    bottom:0;
    right:0;
}
.colors button {
    display:inline-block;
    margin-bottom:.5em;
    padding:.25em .5em;
}
.colors div {
    display:inline-block;
    width:5em;
    height:5em;
}
