.colors div:first-of-type {
    background: rgb(232,85,72);
}
.colors div:nth-of-type(2) {
    background: rgb(191,192,20);
}
.colors div:nth-of-type(3) {
    background: rgb(136, 108, 76);
}
.colors div:nth-of-type(4) {
    background: rgb(153, 158, 159);
}
.colors div:nth-of-type(5) {
    background: rgb(255, 165, 0);
}
.colors div:nth-of-type(6) {
    background: rgb(255, 218, 185);
}
.colors div:nth-of-type(7) {
    background: rgb(195, 220, 217);
}